import Vue from "vue";
import "./plugins/vuetify";
import App from "./App.vue";
import router from "./router";
import store from "./store";
import "./registerServiceWorker";
import axios from "./myAxios";
import VueAxios from "vue-axios";
import { format as dateFormat } from "date-fns";
// ACL
import { abilitiesPlugin } from "@casl/vue";
import { define } from "./resources/ability";
//


Vue.config.productionTip = false;

Vue.filter("dateFormat", (date, format = "DD. MM. YYYY") => {
  return date ? dateFormat(date, format) : '';
});
Vue.filter("employeeName", (item, defaultValue = "") => {
  return item
    ? `${item.degree} ${item.firstName} ${item.lastName}`
    : defaultValue;
});
Vue.mixin({
  methods: {
    toJson(data) {
      try {
        return JSON.parse(data);
      } catch (e) {
        return data;
      }
    }
  }
});
Vue.use(VueAxios, axios);

store.dispatch("loginUserByToken").finally(() => {
  const abilities = define();
  Vue.use(abilitiesPlugin, abilities);

  new Vue({
    router,
    store,
    render: h => h(App)
  }).$mount("#app");
})
