import axios from "../myAxios";

export default {
  state: {
    answers: []
  },
  getters: {},
  mutations: {},
  actions: {
    async loadAllAnswers({ state }, reload = false) {
      if (reload || state.answers.length === 0) {
        axios.get("/answers").then(response => {
          state.answers = response.data;
        });
      }
    }
  }
};
