import axios from "../myAxios";

export default {
  state: {
    questions: []
  },
  getters: {},
  mutations: {},
  actions: {
    async loadAllQuestions({ state }, reload = false) {
      if (reload || state.questions.length === 0) {
        axios.get("/questions").then(response => {
          state.questions = response.data;
        });
      }
    }
  }
};
