import axios from "../myAxios";

export default {
  state: {
    languages: []
  },
  getters: {},
  actions: {
    async loadAllLanguages({ state }, reload = false) {
      if (reload || state.languages.length === 0) {
        axios.get("/languages").then(response => {
          state.languages = response.data;
        });
      }
    }
  }
};
