import { DialogQuestion } from "../components";
export default {
  methods: {
    async editItem(id = 0) {
      let question = {};
      if (id) {
        const result = await this.axios.get(`/questions/${id}`);
        question = result.data;
      }
      this.$store.commit(
        "setComponent",
        <DialogQuestion defaultItem={question} id={id} />
      );
    }
  }
};
