<template>
  <div>
    <v-layout
      my-3
      display-2
      align-center
      justify-center
    >Translations</v-layout>
    <v-card>
      <v-card-text>
        <div
          style="margin-bottom: 16px;"
          xs12
          color="white"
        >
          <v-flex
            xs12
            sm5
          >

          </v-flex>
        </div>
        <v-data-table
          disable-initial-sort
          :search=search
          :headers="headers"
          :items="rows"
          class="elevation-3"
          item-key="id"
          hide-actions
        >
          <template
            slot="items"
            slot-scope="props"
          >
            <tr>
              <td class="text-xs-center">{{ props.item.name }}</td>
              <td class="text-xs-center">
                <v-edit-dialog lazy> {{ props.item.value }} <v-icon
                    small
                    class="mr-2"
                    title="Edit"
                  >
                    edit
                  </v-icon>
                  <v-text-field
                    slot="input"
                    label="Edit"
                    v-bind:value="props.item.value"
                    v-on:change="val => save(props.item.langCode, props.item.name, val)"
                    single-line
                    counter="counter"
                  ></v-text-field>
                </v-edit-dialog>
              </td>
              <td class="text-xs-center">{{ props.item.langName }}</td>
            </tr>
          </template>
          <v-alert
            slot="no-results"
            :value="true"
            color="error"
            icon="warning"
          >
            No result found for "{{ search }}".
          </v-alert>
        </v-data-table>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import { find, propOr, propEq, filter, pipe } from "ramda";
export default {
  data: () => ({
    search: "",
    headers: [
      { text: "Name", value: "name", align: "center" },
      { text: "Translation", value: "value", align: "center" },
      { text: "Language", value: "lang", align: "center" }
    ],
    data: []
  }),
  computed: {
    rows() {
      let newData = [];
      this.$store.state.language.languages.forEach(element => {
        const translateValue = pipe(filter(propEq("lang", element.iso_code)))(
          this.data
        );
        this.$route.query.names.forEach((name, index) => {
          newData.push({
            langName: element.name,
            langCode: element.iso_code,
            name: name,
            value: propOr(
              this.$route.query.defaults[index],
              "value",
              find(propEq("name", name), translateValue)
            )
          });
        });
      });
      return newData;
    }
  },
  created() {
    this.axios
      .get(`/translations`, {
        params: {
          filter: { names: this.$route.query.names }
        }
      })
      .then(response => {
        this.data = response.data;
      });
    this.$store.dispatch("loadAllLanguages");
  },
  watch: {},
  methods: {
    save(lang, name, data) {
      this.axios
        .post("/translations", { value: data, name, lang })
        .then(response => {
          this.axios
            .get(`/translations`, {
              params: {
                filter: { names: this.$route.query.names }
              }
            })
            .then(response => {
              this.data = response.data;
            });
        });
    }
  }
};
</script>