import axios from "../myAxios";
import { find, propEq } from "ramda";

export default {
  state: {
    sections: []
  },
  getters: {
    getSectionById: state => id => {
      return find(propEq("id", id), state.sections);
    }
  },
  mutations: {},
  actions: {
    async loadAllSections({ state }, reload = false) {
      if (reload || state.sections.length === 0) {
        axios.get("/sections").then(response => {
          state.sections = response.data;
        });
      }
    }
  }
};
