<template>
  <v-app id="inspire">
    <v-content v-if="$route.name == 'login'">
      <router-view />
    </v-content>
    <template v-else>
      <v-navigation-drawer
        :clipped="$vuetify.breakpoint.lgAndUp"
        v-model="drawer"
        fixed
        app
      >
        <v-list dense>
          <template v-for="item in itemsByPermissions">
            <v-layout
              v-if="item.heading"
              :key="item.heading"
              row
              align-center
            >
              <v-flex xs6>
                <v-subheader v-if="item.heading">
                  {{ item.heading }}
                </v-subheader>
              </v-flex>
              <v-flex
                xs6
                class="text-xs-center"
              >
                <a
                  href="#!"
                  class="body-2 black--text"
                >EDIT</a>
              </v-flex>
            </v-layout>
            <v-list-group
              v-else-if="item.children"
              v-model="item.model"
              :key="item.text"
              :prepend-icon="item.model ? item.icon : item['icon-alt']"
              append-icon=""
            >
              <v-list-tile slot="activator">
                <v-list-tile-content>
                  <v-list-tile-title>
                    {{ item.text }}
                  </v-list-tile-title>
                </v-list-tile-content>
              </v-list-tile>
              <router-link
                tag="v-list-tile"
                v-for="(child, i) in item.children"
                :to="child.path"
                :key="i"
              >
                <v-list-tile-action v-if="child.icon">
                  <v-icon>{{ child.icon }}</v-icon>
                </v-list-tile-action>
                <v-list-tile-content>
                  <v-list-tile-title>
                    {{ child.text }}
                  </v-list-tile-title>
                </v-list-tile-content>
              </router-link>
            </v-list-group>
            <router-link
              tag="v-list-tile"
              v-else
              :key="item.text"
              :to="item.path"
            >
              <v-list-tile-action>
                <v-icon>{{ item.icon }}</v-icon>
              </v-list-tile-action>
              <v-list-tile-content>
                <v-list-tile-title>
                  {{ item.text }}
                </v-list-tile-title>
              </v-list-tile-content>
            </router-link>
          </template>
        </v-list>
      </v-navigation-drawer>
      <v-toolbar
        :clipped-left="$vuetify.breakpoint.lgAndUp"
        color="blue darken-3"
        dark
        app
        fixed
      >
        <v-toolbar-title
          style="width: 300px"
          class="ml-0 pl-3"
        >
          <v-toolbar-side-icon @click.stop="drawer = !drawer"></v-toolbar-side-icon>
          <router-link
            tag="span"
            to="/"
            style="cursor: pointer;"
          >
            Název aplikace
          </router-link>
        </v-toolbar-title>
        <v-spacer></v-spacer>
      </v-toolbar>
      <v-content>
        <router-view />
      </v-content>
    </template>
    <component
      v-bind:is="mainModal"
      :myData=mainModalData
    ></component>
    <render-component :component="getComponent" />
  </v-app>
</template>

<script>
import { propEq, find, prop, filter, remove } from "ramda";
import RenderComponent from "./components/RenderComponent";
export default {
  components: {
    "render-component": RenderComponent
  },
  data: () => ({
    dialog: false,
    drawer: false,
    items: [
      {
        icon: "tablet_android",
        text: "Forms",
        path: "/forms"
      },
      {
        icon: "tablet_android",
        text: "Sections",
        path: "/sections"
      },
      {
        icon: "tablet_android",
        text: "Questions",
        path: "/"
      },
      {
        icon: "tablet_android",
        text: "Answers",
        path: "/answers"
      },
      {
        icon: "tablet_android",
        text: "Users",
        path: "/users"
      },
      { icon: "power_settings_new", text: "Log out", path: "/fe/logout" }
    ]
  }),
  computed: {
    mainModal() {
      return this.$store.state.mainModal;
    },
    getComponent() {
      return this.$store.state.component;
    },
    mainModalData() {
      return this.$store.state.mainModal
        ? this.$store.state.mainModal.myData
        : null;
    },
    itemsByPermissions() {
      let items = this.items;
      return filter(
        ({ auth }) => (auth ? this.$ability.can(auth[0], auth[1]) : true),
        items
      );
    }
  },
  created() {}
};
</script>
