<template>
  <v-dialog
    :value="true"
    persistent
  >
    <v-card>
      <v-card-title>
        <span class="headline">{{ formTitle }}</span>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>
            <v-flex
              xs12
              sm6
              md4
            >
              <v-text-field
                v-model="editedItem.name"
                label="Name"
              />
            </v-flex>
            <v-flex
              xs12
              sm6
              md4
            >
              <v-text-field
                v-model="editedItem.index"
                label="Index"
                type="number"
              />
            </v-flex>
            <v-flex
              xs12
              sm6
              md4
            >
              <form-select
                v-on:change="(value) => {editedItem.form_id = value.id}"
                :value="editedItem.form_id"
                :multiple="false"
              />
            </v-flex>
            <v-flex
              xs12
            >
              <v-textarea
                v-model="editedItem.description"
                label="Description"
              />
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="blue darken-1"
          flat
          @click.native="close"
        >Zrušit</v-btn>
        <v-btn
          color="blue darken-1"
          flat
          @click.native="save"
        >Uložit</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import FormSelect from "../../../form/components/FormSelect";

export default {
  components: {
    "form-select": FormSelect
  },
  props: ["defaultItem", "id"],
  data: () => ({
    editedItem: {}
  }),
  computed: {
    formTitle() {
      return this.editedItem.name ? "Edit section" : "New section";
    }
  },
  created() {
    this.editedItem = this.defaultItem;
  },
  methods: {
    close() {
      this.$store.commit("setComponent", null);
    },
    save() {
      let url = "/sections";
      if (this.id > 0) {
        url += "/" + this.id;
      }
      this.axios.post(url, this.editedItem).then(response => {
        this.$store.dispatch("loadAllSections", true);
      });
      this.close();
    }
  }
};
</script>