import axios from "../myAxios";

export default {
  state: {
    groups: [],
    transformGroups: []
  },
  getters: {},
  mutations: {},
  actions: {
    async loadAllGroups({ state }, reload = false) {
      if (reload || state.groups.length === 0) {
        axios.get("/groups").then(response => {
          state.groups = response.data;
          state.transformGroups = response.data.reduce(
            (acc, group) => {
              return {
                ...acc,
                [group.id]: group
              };
            },
            {}
          );
        });
      }
    }
  }
};
