import axios from "../myAxios";
import { find, propEq } from 'ramda';

export default {
  state: {
    forms: []
  },
  getters: {
    getFormById: state => id => {
      return find(propEq("id", id), state.forms);
    }
  },
  mutations: {},
  actions: {
    async loadAllForms({ state }, reload = false) {
      if (reload || state.forms.length === 0) {
        axios.get("/forms").then(response => {
          state.forms = response.data;
        });
      }
    }
  }
};
