import Vue from "vue";
import Router from "vue-router";
import {
  Answer,
  Form,
  Group,
  Home,
  Question,
  Section,
  Translation,
  User
} from "./views";
import { define } from "./resources/ability";
import { path, propEq, find } from "ramda";

const requireAuth = (to, from, next) => {
  const abilities = define();
  const permission = path(["meta", "permission"], to);
  if (permission && abilities.cannot(permission[0], permission[1])) {
    const redirect = path(["meta", "redirect"], to);
    const page = find(propEq("actions", "page"), abilities.rules);
    if (redirect) {
      return next(redirect);
    } else if (page) {
      alert("Nemáte dostatečná oprávnění");
      return next("/");
    }
    if(process.env.NODE_ENV === "production") {
      location.href = "/login"
    } 
    return next("/login");
  }
  return next();
};

Vue.use(Router);

export default new Router({
  mode: "history",
  base: process.env.NODE_ENV === "production" ? "/admin" : "/",
  routes: [
    {
      path: "/home",
      name: "home",
      component: Home,
      beforeEnter: requireAuth,
      meta: {
        permission: ["page", "backend"]
      }
    },
    {
      path: "/",
      name: "question",
      component: Question,
      beforeEnter: requireAuth,
      meta: {
        permission: ["page", "backend"]
      }
    },
    {
      path: "/sections",
      name: "section",
      component: Section,
      beforeEnter: requireAuth,
      meta: {
        permission: ["page", "backend"]
      }
    },
    {
      path: "/forms",
      name: "form",
      component: Form,
      beforeEnter: requireAuth,
      meta: {
        permission: ["page", "backend"]
      }
    },
    {
      path: "/translation",
      name: "translation",
      component: Translation,
      beforeEnter: requireAuth,
      meta: {
        permission: ["page", "backend"]
      }
    },
    {
      path: "/answers",
      name: "answers",
      component: Answer,
      beforeEnter: requireAuth,
      meta: {
        permission: ["page", "backend"]
      }
    },
    {
      path: "/users",
      name: "users",
      component: User,
      beforeEnter: requireAuth,
      meta: {
        permission: ["page", "admin"]
      }
    },
    {
      path: "/groups",
      name: "Groups",
      component: Group,
      beforeEnter: requireAuth,
      meta: {
        permission: ["page", "admin"]
      }
    },
    {
      path: "/fe/logout",
      name: "logout",
      beforeEnter: (to, from, next) => {
        localStorage.basicToken = "";
        location.href = "/";
      }
    }
  ]
});
