<template>
  <v-dialog
    :value="true"
    persistent
  >
    <v-card>
      <v-card-title>
        <span class="headline">{{ formTitle }}</span>
      </v-card-title>
      <v-card-text>
        <v-container grid-list-md>
          <v-layout wrap>

            <v-flex
              xs12
              sm6
              md4
            >
              <section-select
                v-on:change="(value) => {editedItem.section_id = value.id}"
                :value="editedItem.section_id"
                :multiple="false"
              />
            </v-flex>
            <v-flex
              xs12
              sm6
              md4
            >
              <v-text-field
                v-model="editedItem.index"
                label="Index"
                type="number"
              />
            </v-flex>
            <!--
            <v-flex
              xs12
              sm6
              md4
            >
              <v-text-field
                label="Select Image"
                @click='pickFile'
                prepend-icon='attach_file'
              ></v-text-field>
              <input
                type="file"
                style="display: none"
                ref="image"
                accept="image/*"
                @change="onFilePicked"
              >
            </v-flex>
            -->
            <v-flex xs12>
              <v-textarea
                v-model="editedItem.text"
                label="Question"
              />
            </v-flex>
          </v-layout>
        </v-container>
      </v-card-text>
      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="blue darken-1"
          flat
          @click.native="close"
        >Zrušit</v-btn>
        <v-btn
          color="blue darken-1"
          flat
          @click.native="save"
        >Uložit</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<script>
import { forEachObjIndexed } from "ramda";
import SectionSelect from "../../../section/components/SectionSelect";

export default {
  components: {
    "section-select": SectionSelect
  },
  props: ["defaultItem", "id"],
  data: () => ({
    editedItem: {}
  }),
  computed: {
    formTitle() {
      return this.editedItem.name ? "Edit question" : "New question";
    }
  },
  created() {
    this.editedItem = this.defaultItem;
  },
  methods: {
    close() {
      this.$store.commit("setComponent", null);
    },
    save() {
      let url = "/questions";
      if (this.id > 0) {
        url += "/" + this.id;
      }
      let formData = new FormData();
      forEachObjIndexed((item, key) => {
        formData.append(key, item);
      }, this.editedItem);

      this.axios
        .post(url, formData, {
          headers: {
            "Content-Type": "multipart/form-data"
          }
        })
        .then(response => {
          this.$store.dispatch("loadAllQuestions", true);
        });
      this.close();
    },
    onFilePicked(e) {
      this.editedItem.img = e.target.files[0];
    },
    pickFile() {
      this.$refs.image.click();
    }
  }
};
</script>