import { DialogForm } from "../components";
export default {
  methods: {
    async editItem(id = 0) {
      let form = {};
      if (id) {
        const result = await this.axios.get(`/forms/${id}`);
        form = result.data;
      }
      this.$store.commit(
        "setComponent",
        <DialogForm defaultItem={form} id={id} />
      );
    }
  }
};
