<template>
  <v-select v-bind="currentProps" :items="forms" @change="changeInner" item-text="name" item-value="id" />
</template>

<script>
export default {
  props: {
    label: {
      type: String,
      default: "Forms"
    },
    multiple: {
      type: Boolean,
      default: false
    },
    "persistent-hint": {
      type: Boolean,
      default: true
    },
    "return-object": {
      type: Boolean,
      default: true
    },
    change: Function,
    value: {
      default: undefined
    },
    rules: Array
  },
  created() {
    this.$store.dispatch("loadAllForms");
  },
  computed: {
    forms() {
      return this.$store.state.form.forms;
    },
    currentProps() {
      return this.$props;
    }
  },
  methods: {
    changeInner(values) {
      return this.change ? this.change(values) : this.$emit("change", values);
    }
  }
};
</script>