<template>
  <v-dialog
    :value="true"
    persistent
  >
    <v-card>
      <v-card-title>
        <span class="headline">Detail answer</span>
      </v-card-title>

      <v-layout
        row
        wrap
      >
        <v-flex
          xs12
          md6
        >
          <v-card-text>Customer: {{item.customer}}</v-card-text>
        </v-flex>
        <v-flex
          xs12
          md6
        >
          <v-card-text>Location: {{item.location}}</v-card-text>
        </v-flex>
        <v-flex
          xs12
          md6
        >
          <v-card-text>Technician: {{item.technician}}</v-card-text>
        </v-flex>
        <v-flex
          xs12
          md6
        >
          <v-card-text>Date: {{item.date | dateFormat}}</v-card-text>
        </v-flex>
      </v-layout>
      <v-expansion-panel>
        <v-expansion-panel-content
          v-for="question in questions"
          :key="question.question_id"
          :expand-icon="question.answer === 'Yes' ? 'not' : undefined"
        >
          <div slot="header">
            <v-layout
              row
              wrap
            >
              <v-flex xs1>
                {{question.section_index}}.{{question.question_index}}
              </v-flex>
              <v-flex xs9>
                {{question.question_text}}
              </v-flex>
              <v-flex
                offset-xs1
                xs1
              >
                {{question.answer}}
              </v-flex>
            </v-layout>
          </div>
          <v-card
            v-if="question.answer === 'No' && findings[question.question_id]"
            class="grey lighten-3"
          >
            <v-layout
              row
              wrap
            >
              <v-flex
                class="text-xs-center"
                xs5
              >
                {{findings[question.question_id].root_cause}} </v-flex>
              <v-flex
                class="text-xs-center"
                xs5
              >
                {{findings[question.question_id].corrective_actions}}
              </v-flex>
              <v-flex xs2>
                {{findings[question.question_id].due_date | dateFormat }}
              </v-flex>
            </v-layout>
          </v-card>
        </v-expansion-panel-content>
      </v-expansion-panel>

      <v-card-actions>
        <v-spacer></v-spacer>
        <v-btn
          color="blue darken-1"
          flat
          @click.native="close"
        >Zrušit</v-btn>
      </v-card-actions>
    </v-card>
  </v-dialog>
</template>

<style scoped>
table {
  border-collapse: collapse;
}
th {
  text-align: left;
  border: 1px solid #ddd;
}
td {
  border: 1px solid #ddd;
  text-align: center;
}
</style>

<script>
import { ascend, prop, sortWith } from "ramda";
export default {
  props: ["item"],
  data: () => ({}),
  computed: {
    questions() {
      return sortWith(
        [ascend(prop("section_index")), ascend(prop("question_index"))],
        this.item.questions
      );
    },
    findings() {
      return this.item.findings.reduce((acc, item) => {
        return {
          ...acc,
          [item.question_id]: item
        };
      }, {});
    }
  },
  created() {},
  methods: {
    close() {
      console.log(this.findings);
      this.$store.commit("setComponent", null);
    }
  }
};
</script>