<template>
  <v-icon small @click="deleteItem(id)" title="Archive">
    delete
  </v-icon>
</template>

<script>
export default {
  props: ["id"],
  methods: {
    async deleteItem(id) {
      if (
        confirm(
          `Are you sure you want to archive this group?`
        )
      ) {
        await this.axios.delete(`/groups/${id}`);
        this.$store.dispatch("loadAllGroups", true);
      }
    }
  },
  // auth(ability) {
  //   return ability.can("DeleteButton", "User");
  // }
};
</script>
