<template>
  <div>
    <v-layout
      my-3
      display-2
      align-center
      justify-center
    >Questions</v-layout>
    <v-card>
      <v-card-text>
        <div
          style="margin-bottom: 16px;"
          xs12
          color="white"
        >
          <div style="float: left;">
            <new />
          </div>
          <v-flex
            xs12
            sm5
          >
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-flex>
        </div>
        <v-data-table
          disable-initial-sort
          :search=search
          :headers="headers"
          :items="questions"
          class="elevation-3"
          item-key="id"
          hide-actions
        >
          <template
            slot="items"
            slot-scope="{item}"
          >
            <tr>
              <td class="text-xs-center">{{ item.text }}</td>
              <td class="text-xs-center">{{ item.section_name }}</td>
              <td class="text-xs-center">{{ item.index }}</td>
              <td class="justify-center layout px-0">
                <edit :id="item.id" />
                <delete :id="item.id" />
                <v-icon
                  small
                  class="mr-2"
                  @click="translate(item)"
                >language</v-icon>
              </td>
            </tr>
          </template>
          <v-alert
            slot="no-results"
            :value="true"
            color="error"
            icon="warning"
          >
            No result found for "{{ search }}".
          </v-alert>
        </v-data-table>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import { prop } from "ramda";
import {
  DeleteButton,
  NewButton,
  EditButton
} from "../module/question/components";
export default {
  components: {
    new: NewButton,
    edit: EditButton,
    delete: DeleteButton
  },
  data: () => ({
    search: "",
    headers: [
      { text: "Question", value: "text", align: "center" },
      { text: "Section", value: "section_name", align: "center" },
      { text: "Index", value: "index", align: "center" },
      { text: "Actions", value: "action", align: "center" }
    ]
  }),
  computed: {
    questions() {
      return this.$store.state.question.questions;
    }
  },
  created() {
    this.$store.dispatch("loadAllQuestions");
  },
  watch: {},
  methods: {
    translate({id, text}) {
      this.$router.push({
        path: `/translation`,
        query: { defaults: [text], names: [`question[${id}].text`] }
      });
    }
  }
};
</script>