import { DialogUser } from "../components";
export default {
  methods: {
    async editItem(id = 0) {
      //if (this.$ability.can("EditButton", "User")) {
      let user = {};
      if (id) {
        const result = await this.axios.get(`/users/${id}`);
        user = result.data;
      }
      this.$store.commit(
        "setComponent",
        <DialogUser defaultItem={user} id={id} />
      );
      //}
    }
  }
};
