<template>
  <div>
    <v-layout
      my-3
      display-2
      align-center
      justify-center
    >Sections</v-layout>
    <v-card>
      <v-card-text>
        <div
          style="margin-bottom: 16px;"
          xs12
          color="white"
        >
          <div style="float: left;">
            <new />
          </div>
          <v-flex
            xs12
            sm5
          >
            <v-text-field
              v-model="search"
              append-icon="search"
              label="Search"
              single-line
              hide-details
            ></v-text-field>
          </v-flex>
        </div>
        <v-data-table
          disable-initial-sort
          :search=search
          :headers="headers"
          :items="sections"
          class="elevation-3"
          item-key="id"
          hide-actions
        >
          <template
            slot="items"
            slot-scope="{item}"
          >
            <tr>
              <td class="text-xs-center">{{ item.name }}</td>
              <td class="text-xs-center">{{ item.description }}</td>
              <td class="text-xs-center">{{ item.form_name }}</td>
              <td class="text-xs-center">{{ item.index }}</td>
              <td class="justify-center layout px-0">
                <edit :id="item.id" />
                <delete :id="item.id" />
                <v-icon
                  small
                  class="mr-2"
                  @click="translate(item)"
                >language</v-icon>
              </td>
            </tr>
          </template>
          <v-alert
            slot="no-results"
            :value="true"
            color="error"
            icon="warning"
          >
            No result found for "{{ search }}".
          </v-alert>
        </v-data-table>
      </v-card-text>
    </v-card>
  </div>
</template>

<script>
import { prop } from "ramda";
import {
  DeleteButton,
  NewButton,
  EditButton
} from "../module/section/components";
export default {
  components: {
    new: NewButton,
    edit: EditButton,
    delete: DeleteButton
  },
  data: () => ({
    search: "",
    headers: [
      { text: "Name", value: "name", align: "center" },
      { text: "Description", value: "description", align: "center" },
      { text: "Form", value: "form_name", align: "center" },
      { text: "Index", value: "index", align: "center" },
      { text: "Akce", value: "action", align: "center" }
    ]
  }),
  computed: {
    sections() {
      return this.$store.state.section.sections;
    }
  },
  created() {
    this.$store.dispatch("loadAllSections");
  },
  watch: {},
  methods: {
    translate({id, name, description}) {
      this.$router.push({
        path: `/translation`,
        query: { defaults: [name, description], names: [`section[${id}].name`, `section[${id}].description`] }
      });
    }
  }
};
</script>