import { DialogDetail, DialogSection } from "../components";
export default {
  methods: {
    async editItem(id = 0) {
      let section = {};
      if (id) {
        const result = await this.axios.get(`/sections/${id}`);
        section = result.data;
      }
      this.$store.commit(
        "setComponent",
        <DialogSection defaultItem={section} id={id} />
      );
    },
    async detailItem(id = 0) {
      const result = await this.axios.get(`/answers/${id}/detail`);
      const answer = result.data;
      
      this.$store.commit(
        "setComponent",
        <DialogDetail item={answer}/>
      );
    }
  }
};
