import { DialogEdit } from "../components";
export default {
  methods: {
    async editItem(id = 0) {
      let defaultItem = {};
      if (id) {
        const result = await this.axios.get(`/groups/${id}`);
        defaultItem = result.data;
      }
      this.$store.commit(
        "setComponent",
        <DialogEdit defaultItem={defaultItem} id={id} />
      );
    }
  }
};
